$bg-gradient: linear-gradient(101.56deg, #2C2C2C 1.56%, #1C1C1C 95.55%);
$font-color: #E4E4E4;
$btn-gradient: linear-gradient(87.32deg, #E2A300 4.47%, #E29500 95.53%);
$btn-shadow: 0px 2px 13px rgba(226, 158, 0, 0.48);
$skills-size: 270px;
$skills-tab-size: 100px;
$skills-radius: 29px;
$skills-bg: #2A2A2A;
$skills-shadow: 0px 4px 6px rgba(0, 0, 0, 0.47);
$main-bg: linear-gradient(130.68deg, #414141 0%, #2D2D2D 100.52%);
$arodora-font: 'Conv_ArodoraPro-Light';
$next-art-bold: 'Conv_NEXT ART_Bold';
$next-art-reg: 'Conv_NEXT ART_Regular';

@font-face {
	font-family: 'Conv_ArodoraPro-Light';
	src: url('../fonts/ArodoraPro-Light.eot');
	src: local('☺'), url('../fonts/ArodoraPro-Light.woff') format('woff'), url('../fonts/ArodoraPro-Light.ttf') format('truetype'), url('../fonts/ArodoraPro-Light.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Conv_NEXT ART_Bold';
	src: url('../fonts/NEXT ART_Bold.eot');
	src: local('☺'), url('../fonts/NEXT ART_Bold.woff') format('woff'), url('../fonts/NEXT ART_Bold.ttf') format('truetype'), url('../fonts/NEXT ART_Bold.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Conv_NEXT ART_Regular';
	src: url('../fonts/NEXT ART_Regular.eot');
	src: local('☺'), url('../fonts/NEXT ART_Regular.woff') format('woff'), url('../fonts/NEXT ART_Regular.ttf') format('truetype'), url('../fonts/NEXT ART_Regular.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}



header {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    background: $bg-gradient;
    nav {
        width: 40%;
        display: flex;
        justify-content: space-between; 
        a {
            box-sizing: border-box;
            text-decoration: none;
            color: $font-color;
            font-family: $arodora-font;
            padding: 5px 30px;
        }   
        a:hover {
            text-decoration: underline orange;
            transition: all 0.5s;
        }
    }
}
div.intro {
    color: $font-color;
    display: flex;
    justify-content: center;
    background: $bg-gradient;
    div.intro-message {
        width: 377px;
        height: 447px;
        h2 {
            font-weight: 600;
            font-size: 48px;
            line-height: 54px;
            font-family: $next-art-bold;
        }
        h1 {
            font-weight: 700;
            font-size: 72px;
            line-height: 82px;
            font-family: $next-art-bold;
        }
        p {
            font-size: 18px;
            line-height: 21px;
            font-family: $arodora-font;
        }
        button {
            background: $btn-gradient;
            box-shadow: $btn-shadow;
            font-family: $arodora-font;
            border: none;
            width: 200px;
            height: 47px;
            border-radius: 16px;
            font-weight: 700;
            font-size: 18px;
            text-transform: uppercase;
            color: black;
        }
    }
    img {
        background-image: url('../images/Ellipse 1.png');
        padding: 00px 200px 00px 200px;
        background-position: 50% 20%;
        background-repeat: no-repeat;
    }
    button {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        border: none;
        background: linear-gradient(130.68deg, #414141 0%, #2D2D2D 100.52%);
        font-family: $next-art-bold;
        font-weight: 700;
        font-size: 36px;
        line-height: 41px;

        display: flex;
        align-items: center;
        justify-content: center;

        color: #00C4F0;
        cursor: pointer;
    }

    button:hover {
        transform: scale(0.9);
        transition: 0.5s;
    }

    button.first {
        position: absolute;
        margin-top: 150px;
        margin-right: 100px;
    }
    button.second {
        position: absolute;
        margin-right: 200px;
        margin-top: 300px;
    }
    button.third {
        position: absolute;
        margin-top: 150px;
        margin-left: 800px;
    }
    button.fourth {
        position: absolute;
        margin-top: 450px;
        margin-right: 160px;
    }
}
main {
    padding-top: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $main-bg;
    flex-direction: column;
    div.skills {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        width: 1180px;
        div.skill-box {
            cursor: pointer;
            margin: 10px;
            width: $skills-size;
            height: $skills-size;
            background: $skills-bg;
            border-radius: $skills-radius;
            box-shadow: $skills-shadow;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            font-size: 24px;
            font-family: $arodora-font;
            line-height: 29px;
        }
        color: $font-color;
        div.skill-box:hover {
            transform: scale(0.9);
            transition: 0.5s;
        }
    }
    div.about-me {
        width: 100%;
        background-image: url("../images/Ellips.png");
        background-repeat: no-repeat;
        align-items: center;
        background-position: 50% 30%;
        background-size: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 200px;
        h2 {
            font-weight: 400;
            font-size: 48px;
            line-height: 54px;
            text-transform: uppercase;
            color: $font-color;
            position: absolute;
            text-align: center;
            margin-bottom: 800px;
            font-family: $next-art-reg;
        }
        p {
            margin-top: 70px;
            font-size: 20px;
            width: 496px;
            background-color: #2C2C2C;
            color: $font-color;
            padding: 63px;
            box-sizing: border-box;
            border-radius: 14px;
            font-family: $arodora-font;
        }
        img {
            border: 16px solid #2C2C2C;
            border-radius: 14px;
        }
        img.first {
            position: absolute;
            margin-right: 800px;
            margin-bottom: 400px;
        }
        img.second {
            position: absolute;
            margin-left: 750px;
        }
        img.third {
            margin-top: 500px;
            margin-right: 750px;
            position: absolute;
        }
    }
    div.portfolio {
        background: $bg-gradient;
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        padding-bottom: 140px;
        padding-top: 140px;
        button {
            width: 60px;
            height: 60px;
            border-radius: 50%;
            border: none;
            color: $font-color;
            background-color: orange;
            font-size: 24px;
            margin-left: 20px;
            justify-content: center;
            cursor: pointer;
        }
        button:hover {
            transform: scale(0.9);
            transition: 0.5s;
        }
        h2 {
            position: absolute;
            text-align: center;
            color: $font-color;
            text-transform: uppercase;
            font-weight: 400;
            font-size: 48px;
            line-height: 54px;
            margin-bottom: 760px;
            font-family: $next-art-reg;
        }
        div.portfolio-info {
            width: 970px;
            padding: 40px;
            box-sizing: border-box;
            background: linear-gradient(130.68deg, #414141 0%, #2D2D2D 100.52%);
            box-shadow: 0px 4px 15px rgba(27, 27, 27, 0.28);
            border-radius: 29px;
            display: flex;
            justify-content: space-around;
            img {
                border-radius: 29px;
            }
            p {
                width: 296px;
                color: $font-color;
                font-weight: 300;
                font-size: 18px;
                line-height: 21px;
                text-align: center;
                font-family: $arodora-font;
                a {
                    line-height: 50px;
                }
                a:hover {
                    text-decoration: underline orange;
                    transition: all 0.5s;
                }
                b {
                    line-height: 50px;
                }
            }
        }
    }
}
footer {
    padding: 40px;
    display: flex;
    justify-content: space-around;
    background: linear-gradient(130.68deg, #414141 0%, #2D2D2D 100.52%);
    font-family: $arodora-font;
    nav {
        display: flex;
        flex-direction: column;
        color: $font-color;
        justify-content: space-around;
        a:link {
            color: $font-color;
            text-decoration: none;
        }
        a:visited {
            color: $font-color;
            text-decoration: none;
        }
        a:hover {
            color: $font-color;
            text-decoration: underline orange;
            transition: all 0.5s;
        }
    }
    div.contacts {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        color:$font-color;
        a:nth-child(1):hover {
            text-decoration: none;
        }
        a:link {
            color: $font-color;
            text-decoration: none;
        }
        a:visited {
            color: $font-color;
            text-decoration: none;
        }
        a:hover {
            text-decoration: underline orange;
            transition: all 0.5s;
        }
    }
    div.footer-info {
        display: flex;
        flex-direction: column;
        color: $font-color;
        div.link {
            display: flex;
            justify-content: space-between;
            button {
                width: 60px;
                height: 60px;
                border-radius: 50%;
                border: none;
                color: $font-color;
                background-color: orange;
                font-size: 24px;
                margin-left: 20px;
                cursor: pointer;
            }
            button:hover {
                transform: scale(0.9);
                transition: 0.5s;
            }
        }
    }
}

@media (max-width: 810px) {
    header {
        nav {
            a {
                padding: 0px 0px;
            }
        }   
    }
    div.intro {
        div.intro-message {
            h1 {
                font-size: 32px;
            }
            h2 {
                font-size: 28px;
            }
            p {
                font-size: 16px;
            }
            button {
                display: block;
            }
        }
        img {
            padding: 0px;
            width: 230px;
            background: none;
        }
        button {
            display: none;
        }
    }
    main {
        div.skills {
            width: 500px;
            div.skill-box {
                width: $skills-tab-size;
                height: $skills-tab-size;
                border-radius: 15px;
                img {
                    width:40px;
                }
                p {
                    font-size: 12px;
                }
            }
        }
        div.about-me {
            background-size: 90%;
            h2 {
                font-size: 28px;
                margin-bottom: 500px;
            }
            p {
                padding: 20px;
                font-size: 14px;
                width: 300px;
            }
            img.first {
                width:180px;
                margin-right: 500px;
                margin-top: 200px;
            }
            img.second {
                width: 200px;
                margin-left: 500px;
            }
            img.third {
                width:180px;
                margin-right: 500px;
            }
        }
        div.portfolio {
            h2 {
                font-size: 28px;
            }
        }
    }
    footer {
        nav {
            a {
                font-size: 14px;
            }
            a:link {
                color: $font-color;
                text-decoration: none;
            }
            a:visited {
                color: $font-color;
                text-decoration: none;
            }
            a:hover {
                color: $font-color;
                text-decoration: underline orange;
                transition: all 0.5s;
            }
        }
        div.contacts {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            color:$font-color;
            a {
                font-size: 14px;
            }
        }
        div.footer-info {
            p {
                font-size: 14px;
            }
            div.link {
                button {
                    width: 30px;
                    height: 30px;
                    font-size: 14px;
                }
            }
        }
    }
}

@media (max-width: 375px) {
    header {
        div.logo {
            img {
                width: 100px;
            }
        }
        nav {
            justify-content: space-around;
            a {
                font-size: 11px;
                padding: 2px;
                text-align: center;
            }
        }
    }
    div.intro {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        div.intro-message {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            h1 {
                font-size: 24px;
                line-height: 10px;
            }
            h2 {
                font-size: 20px;
                line-height: 10px;
            }
            p {
                font-size: 12px;
                width: 240px;
                text-align: center;
            }
        }
    }
    main {
        div.skills {
            width: 300px;
        }
        div.about-me {
            background-size: 110%;
            h2 {
                font-size: 20px;
                margin-bottom: 340px;
            }
            p {
                font-size: 12px;
            }
            img {
                display: none;
            }
        }
        div.portfolio {
            h2 {
                font-size: 20px;
            }
        }
    }
    footer {
        justify-content: space-between;
        padding: 10px;
        nav {
            a {
                font-size: 10px;
            }
        }
        div.contacts {
            a {
                font-size: 10px;
            }
        }
        div.footer-info {
            div.link {
                img {
                    width:100px;
                }
                a {
                    button {
                        width: 30px;
                        height: 30px;
                    }
                }
            }
            p {
                font-size: 10px;
            }
        }
    }
}