header {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  background: linear-gradient(101.56deg, #2C2C2C 1.56%, #1C1C1C 95.55%);
}
header nav {
  width: 40%;
  display: flex;
  justify-content: space-between;
}
header nav a {
  box-sizing: border-box;
  text-decoration: none;
  color: #E4E4E4;
  font-family: "Conv_ArodoraPro-Light";
  padding: 5px 30px;
}
header nav a:hover {
  -webkit-text-decoration: underline orange;
          text-decoration: underline orange;
  transition: all 0.5s;
}

div.intro {
  color: #E4E4E4;
  display: flex;
  justify-content: center;
  background: linear-gradient(101.56deg, #2C2C2C 1.56%, #1C1C1C 95.55%);
}
div.intro div.intro-message {
  width: 377px;
  height: 447px;
}
div.intro div.intro-message h2 {
  font-weight: 600;
  font-size: 48px;
  line-height: 54px;
  font-family: "Conv_NEXT ART_Bold";
}
div.intro div.intro-message h1 {
