  font-size: 72px;
  line-height: 82px;
  font-family: "Conv_NEXT ART_Bold";
}
div.intro div.intro-message p {
  font-size: 18px;
  line-height: 21px;
  font-family: "Conv_ArodoraPro-Light";
}
div.intro div.intro-message button {
  background: linear-gradient(87.32deg, #E2A300 4.47%, #E29500 95.53%);
  box-shadow: 0px 2px 13px rgba(226, 158, 0, 0.48);
  font-family: "Conv_ArodoraPro-Light";
  border: none;
  width: 200px;
  height: 47px;
  border-radius: 16px;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  color: black;
}
div.intro img {
  background-image: url("../images/Ellipse 1.png");
  padding: 0px 200px 0px 200px;
  background-position: 50% 20%;
  background-repeat: no-repeat;
}
div.intro button {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(130.68deg, #414141 0%, #2D2D2D 100.52%);
