  font-weight: 700;
  font-size: 36px;
  line-height: 41px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #00C4F0;
  cursor: pointer;
}
div.intro button:hover {
  transform: scale(0.9);
  transition: 0.5s;
}
div.intro button.first {
  position: absolute;
  margin-top: 150px;
  margin-right: 100px;
}
div.intro button.second {
  position: absolute;
  margin-right: 200px;
  margin-top: 300px;
}
div.intro button.third {
  position: absolute;
  margin-top: 150px;
  margin-left: 800px;
}
div.intro button.fourth {
  position: absolute;
  margin-top: 450px;
  margin-right: 160px;
}

main {
  padding-top: 100px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
