.main-container {
    width: 1115px;
    display: flex;
    align-items: center;
}

.window {
    width:100%;
    height: 100%;
    overflow: hidden;
}

.all-portfolio-info {
    display: flex;
    height: 100%;
    width: 100%;

    transition: translate;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
}